export default {
    props: {
        array: {},  //选项列表
        index: 0  //默认选中的选项下标
    },
    data() {
        return {}
    },
    methods: {
        bindPickerChange: function(e) {  //picker组件选项改变事件处理函数
            this.index = e.target.value
            this.$emit('bind-picker-change', this.array[this.index])  //返回选中的选项值
        }
    }
}
